"use client"

import { useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { ArrowLeft, Building2 } from "lucide-react"
import Link from "next/link"

export default function BusinessRegistrationPage() {
  const [formData, setFormData] = useState({
    email: "",
    password: "",
    confirmPassword: "",
    companyName: "",
    registrationNumber: "",
    registrationDate: "",
    taxNumber: "",
    legalAddress: "",
    physicalAddress: "",
    businessType: "",
    industry: "",
    directorFirstName: "",
    directorLastName: "",
    directorDob: "",
    directorIdNumber: "",
    phoneNumber: "",
    ownershipStructure: "",
    website: ""
  })

  const [files, setFiles] = useState({
    directorIdDocument: null as File | null,
    directorSelfie: null as File | null,
    companyRegistrationCertificate: null as File | null,
    taxRegistrationCertificate: null as File | null
  })

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({ ...prev, [name]: value }))
  }

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, files } = e.target
    if (files && files[0]) {
      setFiles(prev => ({ ...prev, [name]: files[0] }))
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    try {
      const submitFormData = new FormData()
      
      // –î–æ–±–∞–≤–ª—è–µ–º –¥–∞–Ω–Ω—ã–µ —Ñ–æ—Ä–º—ã
      Object.entries(formData).forEach(([key, value]) => {
        if (value) submitFormData.append(key, value)
      })
      
      // –î–æ–±–∞–≤–ª—è–µ–º —Ñ–∞–π–ª—ã
      if (files.directorIdDocument) {
        submitFormData.append('directorIdDocument', files.directorIdDocument)
      }
      if (files.directorSelfie) {
        submitFormData.append('directorSelfie', files.directorSelfie)
      }
      if (files.companyRegistrationCertificate) {
        submitFormData.append('companyRegistrationCertificate', files.companyRegistrationCertificate)
      }
      if (files.taxRegistrationCertificate) {
        submitFormData.append('taxRegistrationCertificate', files.taxRegistrationCertificate)
      }
      
      const response = await fetch('/api/kyc/register/business', {
        method: 'POST',
        body: submitFormData
      })
      
      const result = await response.json()
      
      if (response.ok) {
        // –°–æ—Ö—Ä–∞–Ω—è–µ–º —Ç–æ–∫–µ–Ω
        localStorage.setItem('token', result.token)
        // –ü–µ—Ä–µ–Ω–∞–ø—Ä–∞–≤–ª—è–µ–º –Ω–∞ –¥–µ–º–æ
        window.location.href = '/demo'
      } else {
        alert(result.error || '–û—à–∏–±–∫–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏')
      }
    } catch (error) {
      console.error('Registration error:', error)
      alert('–û—à–∏–±–∫–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏')
    }
  }

  return (
    <div className="min-h-screen bg-background">
      <div className="container mx-auto max-w-4xl px-4 py-20">
        <div className="mb-8">
          <Link href="/kyc" className="inline-flex items-center text-muted-foreground hover:text-foreground transition-colors">
            <ArrowLeft className="h-4 w-4 mr-2" />
            –ù–∞–∑–∞–¥ –∫ –≤—ã–±–æ—Ä—É —Ç–∏–ø–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏
          </Link>
        </div>

        <Card className="border-border/50 bg-card/80 backdrop-blur">
          <CardHeader>
            <CardTitle className="flex items-center gap-3">
              <div className="h-10 w-10 rounded-full bg-accent/20 flex items-center justify-center">
                <Building2 className="h-5 w-5 text-accent" />
              </div>
              –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è –±–∏–∑–Ω–µ—Å–∞
            </CardTitle>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-6">
              {/* –ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ –∫–æ–º–ø–∞–Ω–∏–∏ */}
              <div className="space-y-4">
                <h3 className="text-lg font-semibold text-foreground">–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ –∫–æ–º–ø–∞–Ω–∏–∏</h3>
                <div>
                  <Label htmlFor="companyName">–ù–∞–∑–≤–∞–Ω–∏–µ –∫–æ–º–ø–∞–Ω–∏–∏ *</Label>
                  <Input
                    id="companyName"
                    name="companyName"
                    value={formData.companyName}
                    onChange={handleInputChange}
                    required
                  />
                </div>
                <div className="grid gap-4 md:grid-cols-2">
                  <div>
                    <Label htmlFor="registrationNumber">–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–æ–Ω–Ω—ã–π –Ω–æ–º–µ—Ä *</Label>
                    <Input
                      id="registrationNumber"
                      name="registrationNumber"
                      value={formData.registrationNumber}
                      onChange={handleInputChange}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="registrationDate">–î–∞—Ç–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ *</Label>
                    <Input
                      id="registrationDate"
                      name="registrationDate"
                      type="date"
                      value={formData.registrationDate}
                      onChange={handleInputChange}
                      required
                    />
                  </div>
                </div>
                <div>
                  <Label htmlFor="taxNumber">–ù–∞–ª–æ–≥–æ–≤—ã–π –Ω–æ–º–µ—Ä *</Label>
                  <Input
                    id="taxNumber"
                    name="taxNumber"
                    value={formData.taxNumber}
                    onChange={handleInputChange}
                    required
                  />
                </div>
                <div className="grid gap-4 md:grid-cols-2">
                  <div>
                    <Label htmlFor="legalAddress">–Æ—Ä–∏–¥–∏—á–µ—Å–∫–∏–π –∞–¥—Ä–µ—Å *</Label>
                    <Input
                      id="legalAddress"
                      name="legalAddress"
                      value={formData.legalAddress}
                      onChange={handleInputChange}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="physicalAddress">–§–∏–∑–∏—á–µ—Å–∫–∏–π –∞–¥—Ä–µ—Å *</Label>
                    <Input
                      id="physicalAddress"
                      name="physicalAddress"
                      value={formData.physicalAddress}
                      onChange={handleInputChange}
                      required
                    />
                  </div>
                </div>
                <div className="grid gap-4 md:grid-cols-2">
                  <div>
                    <Label htmlFor="businessType">–¢–∏–ø –±–∏–∑–Ω–µ—Å–∞ *</Label>
                    <Select onValueChange={(value) => setFormData(prev => ({ ...prev, businessType: value }))}>
                      <SelectTrigger>
                        <SelectValue placeholder="–í—ã–±–µ—Ä–∏—Ç–µ —Ç–∏–ø –±–∏–∑–Ω–µ—Å–∞" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="llc">–¢–û–û</SelectItem>
                        <SelectItem value="individual">–ò–ü</SelectItem>
                        <SelectItem value="partnership">–¢–æ–≤–∞—Ä–∏—â–µ—Å—Ç–≤–æ</SelectItem>
                        <SelectItem value="corporation">–ê–û</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                  <div>
                    <Label htmlFor="industry">–û—Ç—Ä–∞—Å–ª—å *</Label>
                    <Select onValueChange={(value) => setFormData(prev => ({ ...prev, industry: value }))}>
                      <SelectTrigger>
                        <SelectValue placeholder="–í—ã–±–µ—Ä–∏—Ç–µ –æ—Ç—Ä–∞—Å–ª—å" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="technology">–¢–µ—Ö–Ω–æ–ª–æ–≥–∏–∏</SelectItem>
                        <SelectItem value="retail">–†–æ–∑–Ω–∏—á–Ω–∞—è —Ç–æ—Ä–≥–æ–≤–ª—è</SelectItem>
                        <SelectItem value="manufacturing">–ü—Ä–æ–∏–∑–≤–æ–¥—Å—Ç–≤–æ</SelectItem>
                        <SelectItem value="services">–£—Å–ª—É–≥–∏</SelectItem>
                        <SelectItem value="agriculture">–°–µ–ª—å—Å–∫–æ–µ —Ö–æ–∑—è–π—Å—Ç–≤–æ</SelectItem>
                        <SelectItem value="construction">–°—Ç—Ä–æ–∏—Ç–µ–ª—å—Å—Ç–≤–æ</SelectItem>
                        <SelectItem value="other">–î—Ä—É–≥–æ–µ</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                </div>
                <div>
                  <Label htmlFor="website">–í–µ–±-—Å–∞–π—Ç (–Ω–µ–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ)</Label>
                  <Input
                    id="website"
                    name="website"
                    type="url"
                    value={formData.website}
                    onChange={handleInputChange}
                  />
                </div>
              </div>

              {/* –ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ –¥–∏—Ä–µ–∫—Ç–æ—Ä–µ */}
              <div className="space-y-4">
                <h3 className="text-lg font-semibold text-foreground">–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ –¥–∏—Ä–µ–∫—Ç–æ—Ä–µ</h3>
                <div className="grid gap-4 md:grid-cols-2">
                  <div>
                    <Label htmlFor="directorFirstName">–ò–º—è –¥–∏—Ä–µ–∫—Ç–æ—Ä–∞ *</Label>
                    <Input
                      id="directorFirstName"
                      name="directorFirstName"
                      value={formData.directorFirstName}
                      onChange={handleInputChange}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="directorLastName">–§–∞–º–∏–ª–∏—è –¥–∏—Ä–µ–∫—Ç–æ—Ä–∞ *</Label>
                    <Input
                      id="directorLastName"
                      name="directorLastName"
                      value={formData.directorLastName}
                      onChange={handleInputChange}
                      required
                    />
                  </div>
                </div>
                <div className="grid gap-4 md:grid-cols-2">
                  <div>
                    <Label htmlFor="directorDob">–î–∞—Ç–∞ —Ä–æ–∂–¥–µ–Ω–∏—è –¥–∏—Ä–µ–∫—Ç–æ—Ä–∞ *</Label>
                    <Input
                      id="directorDob"
                      name="directorDob"
                      type="date"
                      value={formData.directorDob}
                      onChange={handleInputChange}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="directorIdNumber">–ù–æ–º–µ—Ä –¥–æ–∫—É–º–µ–Ω—Ç–∞ –¥–∏—Ä–µ–∫—Ç–æ—Ä–∞ *</Label>
                    <Input
                      id="directorIdNumber"
                      name="directorIdNumber"
                      value={formData.directorIdNumber}
                      onChange={handleInputChange}
                      required
                    />
                  </div>
                </div>
                <div>
                  <Label htmlFor="phoneNumber">–ö–æ–Ω—Ç–∞–∫—Ç–Ω—ã–π —Ç–µ–ª–µ—Ñ–æ–Ω *</Label>
                  <Input
                    id="phoneNumber"
                    name="phoneNumber"
                    type="tel"
                    value={formData.phoneNumber}
                    onChange={handleInputChange}
                    required
                  />
                </div>
                <div>
                  <Label htmlFor="ownershipStructure">–°—Ç—Ä—É–∫—Ç—É—Ä–∞ —Å–æ–±—Å—Ç–≤–µ–Ω–Ω–æ—Å—Ç–∏ (–Ω–µ–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ)</Label>
                  <Input
                    id="ownershipStructure"
                    name="ownershipStructure"
                    value={formData.ownershipStructure}
                    onChange={handleInputChange}
                  />
                </div>
              </div>

              {/* –ó–∞–≥—Ä—É–∑–∫–∞ –¥–æ–∫—É–º–µ–Ω—Ç–æ–≤ */}
              <div className="space-y-4">
                <h3 className="text-lg font-semibold text-foreground">–ó–∞–≥—Ä—É–∑–∫–∞ –¥–æ–∫—É–º–µ–Ω—Ç–æ–≤</h3>
                <div className="space-y-4">
                  <div>
                    <Label htmlFor="directorIdDocument">–î–æ–∫—É–º–µ–Ω—Ç –¥–∏—Ä–µ–∫—Ç–æ—Ä–∞ *</Label>
                    <div className="mt-2">
                      <Input
                        id="directorIdDocument"
                        name="directorIdDocument"
                        type="file"
                        accept="image/*"
                        onChange={handleFileChange}
                        required
                      />
                    </div>
                  </div>
                  <div>
                    <Label htmlFor="directorSelfie">–°–µ–ª—Ñ–∏ –¥–∏—Ä–µ–∫—Ç–æ—Ä–∞ —Å –¥–æ–∫—É–º–µ–Ω—Ç–æ–º *</Label>
                    <div className="mt-2">
                      <Input
                        id="directorSelfie"
                        name="directorSelfie"
                        type="file"
                        accept="image/*"
                        onChange={handleFileChange}
                        required
                      />
                    </div>
                  </div>
                  <div>
                    <Label htmlFor="companyRegistrationCertificate">–°–≤–∏–¥–µ—Ç–µ–ª—å—Å—Ç–≤–æ –æ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ *</Label>
                    <div className="mt-2">
                      <Input
                        id="companyRegistrationCertificate"
                        name="companyRegistrationCertificate"
                        type="file"
                        accept="image/*"
                        onChange={handleFileChange}
                        required
                      />
                    </div>
                  </div>
                  <div>
                    <Label htmlFor="taxRegistrationCertificate">–°–≤–∏–¥–µ—Ç–µ–ª—å—Å—Ç–≤–æ –æ –ø–æ—Å—Ç–∞–Ω–æ–≤–∫–µ –Ω–∞ –Ω–∞–ª–æ–≥–æ–≤—ã–π —É—á–µ—Ç *</Label>
                    <div className="mt-2">
                      <Input
                        id="taxRegistrationCertificate"
                        name="taxRegistrationCertificate"
                        type="file"
                        accept="image/*"
                        onChange={handleFileChange}
                        required
                      />
                    </div>
                  </div>
                </div>
              </div>

              {/* –ê–∫–∫–∞—É–Ω—Ç */}
              <div className="space-y-4">
                <h3 className="text-lg font-semibold text-foreground">–î–∞–Ω–Ω—ã–µ –¥–ª—è –≤—Ö–æ–¥–∞</h3>
                <div>
                  <Label htmlFor="email">Email *</Label>
                  <Input
                    id="email"
                    name="email"
                    type="email"
                    value={formData.email}
                    onChange={handleInputChange}
                    required
                  />
                </div>
                <div className="grid gap-4 md:grid-cols-2">
                  <div>
                    <Label htmlFor="password">–ü–∞—Ä–æ–ª—å *</Label>
                    <Input
                      id="password"
                      name="password"
                      type="password"
                      value={formData.password}
                      onChange={handleInputChange}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="confirmPassword">–ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å *</Label>
                    <Input
                      id="confirmPassword"
                      name="confirmPassword"
                      type="password"
                      value={formData.confirmPassword}
                      onChange={handleInputChange}
                      required
                    />
                  </div>
                </div>
              </div>

              <Button type="submit" className="w-full bg-accent text-accent-foreground hover:bg-accent/90">
                –ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è
              </Button>
            </form>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
